:root {
  --sol-background: #fdf6e3;
  --sol-background-trans: rgba(253, 246, 227, 0.6);
  --sol-highlight:  #eee8d5;
  --sol-highlight-trans: rgba(238, 232, 213, 0.6);
  --sol-primary:    #586e75;
  --sol-secondary:  #657b83; }

[data-theme="dark"] {
  --sol-background: #002b36;
  --sol-background-trans: rgba(0, 43, 54, 0.75);
  --sol-highlight:  #073642;
  --sol-highlight-trans: rgba(7, 54, 66, 0.75);
  --sol-primary:    #93a1a1;
  --sol-secondary:  #839496; }

body {
  margin: 8px; }

.container {
  font-family: "Open Sans", sans-serif;
  display: grid;
  grid-template-columns: 350px auto;
  grid-template-rows: 50px auto 50px;
  grid-column-gap: 15px;
  grid-row-gap: 10px;
  grid-template-areas: "logo main" "sidebar main" "sidebar-footer main-footer";
  min-height: calc(100vh - 20px);
  max-width: 1920px; }

.logo-area {
  grid-area: logo; }
  .logo-area .logo {
    text-align: center;
    background-color: var(--sol-background-trans);
    border-radius: 10px; }
    .logo-area .logo .logo-content {
      font-family: "Open Sans", sans-serif;
      font-weight: 600;
      font-size: 30px;
      line-height: 50px;
      text-decoration: unset; }

.main-area {
  grid-area: main; }
  .main-area .main .heading {
    line-height: 50px;
    padding-left: 10px;
    margin-bottom: 10px;
    background-color: var(--sol-highlight);
    border-radius: 10px; }
    .main-area .main .heading h1 {
      font-family: "Open Sans", sans-serif;
      font-size: 30px;
      margin: 0; }
    .main-area .main .heading .heading-home {
      font-family: "Open Sans", sans-serif;
      font-weight: 600; }

.sidebar-area {
  grid-area: sidebar; }

.main-footer-area {
  grid-area: main-footer; }
  .main-footer-area .main-footer {
    line-height: 50px;
    background-color: var(--sol-highlight);
    border-radius: 10px;
    padding-left: 10px; }

.sidebar-footer-area {
  grid-area: sidebar-footer; }
  .sidebar-footer-area .sidebar-footer {
    text-align: center;
    line-height: 50px;
    background-color: var(--sol-background-trans);
    border-radius: 10px; }

.background {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -42;
  height: 100vh;
  width: 365px;
  overflow: hidden;
  justify-content: center;
  display: flex; }
  .background img {
    width: auto;
    height: 100%; }
  .background img.portrait {
    width: 100%;
    height: auto; }

@media (max-width: 900px), (orientation: portrait), (max-device-width: 767px) {
  body {
    margin: 0; }
  .container {
    display: block; }
  .logo-area {
    padding: 8px 8px 4px 8px;
    background-color: var(--sol-background); }
    .logo-area .logo {
      background-color: var(--sol-highlight); }
  .main-area {
    background-color: var(--sol-background);
    padding: 4px 8px 8px 8px; }
    .main-area.alt {
      padding: 8px 8px 4px 8px; }
  .sidebar-area {
    padding: 8px 8px 4px 8px; }
    .sidebar-area.alt {
      padding: 4px 8px 8px 8px; }
  .main-footer-area {
    padding: 4px 8px 4px 8px; }
    .main-footer-area .main-footer {
      background-color: var(--sol-background-trans); }
  .sidebar-footer-area {
    padding: 4px 8px 8px 8px; }
  .background {
    width: 100%; } }

@media only screen and (max-width: 900px) and (orientation: landscape), only screen and (orientation: portrait) and (orientation: landscape), only screen and (max-device-width: 767px) and (orientation: landscape) {
  .background img {
    width: 100%;
    height: auto; }
  .background img.portrait {
    width: auto;
    height: 100%; } }

@font-face {
  font-family: Open Sans;
  src: url(/static/fonts/OpenSans/OpenSans-Regular.ttf); }

:root {
  background-color: var(--sol-background);
  color: var(--sol-primary); }

a {
  color: var(--sol-primary); }

a:active, a:hover, a:visited {
  color: var(--sol-secondary); }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

h1 {
  font-size: 24px; }
  h1 a, h1 a:active, h1 a:hover, h1 a:visited {
    text-decoration: none;
    color: var(--sol-primary); }

div[onclick] {
  cursor: pointer; }


span.darkmode-switch {
  float: right;
  padding-top: 8px;
  padding-right: 8px; }

label.darkmode-switch {
  position: relative;
  display: inline-block;
  width: 90px;
  height: 34px; }
  label.darkmode-switch input {
    display: none; }
  label.darkmode-switch .darkmode-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #073642;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 10px; }
  label.darkmode-switch .darkmode-slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: #eee8d5;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 10px; }
  label.darkmode-switch input:checked + .darkmode-slider {
    background-color: #eee8d5; }
  label.darkmode-switch input:focus + .darkmode-slider {
    box-shadow: 0 0 1px #268bd2; }
  label.darkmode-switch input:checked + .darkmode-slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(55px);
    background-color: #073642; }
  label.darkmode-switch .darkmode-slider:after {
    content: 'Theme';
    color: #839496;
    display: block;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    font-size: 16px;
    font-weight: 600;
    font-family: "Open Sans", sans-serif; }
  label.darkmode-switch input:checked + .darkmode-slider:after {
    color: #657b83; }

.main .category-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 240px);
  grid-auto-rows: 300px;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  justify-items: stretch;
  align-items: stretch; }
  .main .category-list .category-block {
    position: relative;
    display: flex;
    border-radius: 10px;
    overflow: hidden;
    justify-content: center; }
    .main .category-list .category-block .visited:after {
      font-family: "Font Awesome 5 Free";
      font-weight: 400;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      line-height: 1;
      position: absolute;
      top: 2px;
      right: 2px;
      content: "\f111";
      z-index: 1;
      font-size: 17px; }
    .main .category-list .category-block .visited[data-visited]:after {
      content: "\f058"; }
    .main .category-list .category-block .category-block-head {
      position: absolute;
      background-color: var(--sol-highlight);
      min-height: 50px;
      padding: 5px 0 10px 5px;
      top: 0;
      left: 0;
      width: 235px;
      overflow: hidden; }
      .main .category-list .category-block .category-block-head h3 {
        width: 215px; }
    .main .category-list .category-block img {
      width: auto;
      height: 100%;
      margin-top: 10px; }
    .main .category-list .category-block img.portrait {
      width: 100%;
      height: auto; }

.sidebar .sidebar-articles-block {
  border-radius: 10px;
  background-color: var(--sol-background-trans); }
  .sidebar .sidebar-articles-block h2 {
    padding: 10px; }
  .sidebar .sidebar-articles-block .article-list {
    display: grid;
    grid-template-columns: auto;
    grid-auto-rows: 100px;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
