      .sidebar .sidebar-articles-block .article-list .article .visited:after {
        font-family: "Font Awesome 5 Free";
        font-weight: 400;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        line-height: 1;
        position: absolute;
        top: 2px;
        right: 2px;
        content: "\f111";
        z-index: 1;
        font-size: 17px; }
      .sidebar .sidebar-articles-block .article-list .article .visited[data-visited]:after {
